$background-color_1: #343844;

.loading__container {
	grid-row: 2 / span 1;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding-top: 7vh;
	.loading__container-item {
		width: 30%;
		height: 100%;
		display: flex;
		flex-direction: column;
	}
}
.loading__container-item {
	.loading__container-item--header {
		height: 15px;
		background-color: $background-color_1;
		border-radius: 5px;
		margin-bottom: 60px;
	}
	.loading__container-item--progress-bar {
		background-color: $background-color_1;
		border-radius: 5px;
		margin-bottom: 10px;
	}
}
.flex-container {
	display: flex;
	width: 100%;
	justify-content: space-between;
	div {
		height: 25px;
		background-color: $background-color_1;
		border-radius: 5px;
		width: 45%;
	}
}
@media screen and (max-width: 800px) {
	.loading__container {
		grid-row: 2 / span 1;
		display: grid;
		grid-template-rows: repeat(2, auto);
		grid-template-columns: repeat(2, auto);
		justify-content: initial;
		align-items: inherit;
		.loading__container-item {
			width: 100%;
		}
	}
	.flex-container {
		justify-content: inherit;
		flex-direction: column;
		align-items: end;
		row-gap: 10px;
	}
	.loading__container-item--wide {
		grid-column: 1 / span 2;
	}
}
